import React, { ReactElement } from "react"
import { Helmet } from "react-helmet"
import { graphql } from "gatsby"
import { css } from "@emotion/react"
import markdown_github_style_css from "@/markdownStyle/github"
// import { TableOfContents } from "../components/TableOfContents"
import { TableOfContents } from "@/components/TableOfContents"
import { useLayoutEffect } from "react"
import countingStar from "@/counting_star"

interface node {
  frontmatter: {
    title: string
    tags: string[]
    created: string
  }
  html: string
  tableOfContents: string
}

interface edge {
  node: node
}

const template_layout_css = css`
  display: flex;
  justify-content: center;
  /* align-items: center; */
  width: 100vw;
  height: 100vh;
  padding: 3rem;
`

const context_block_css = css`
  /* display: flex;
  justify-content: center;
  align-items: center; */
  width: 50vw;
`

export default function Template({
  data,
  path,
}: {
  data: any
  path: any
}): ReactElement {
  const {
    allMarkdownRemark: { edges },
  }: { allMarkdownRemark: { edges: edge[] } } = data
  console.log(edges)

  useLayoutEffect(() => {
    countingStar()
  }, [])

  return (
    <div css={template_layout_css}>
      {/* <h2>{edges[0].node.frontmatter.created}</h2>
      <h2>{edges[0].node.frontmatter.tags}</h2> */}
      <Helmet
        meta={[
          { property: "og:title", content: edges[0].node.frontmatter.title },
          // { property: "og:description", content: "포스트의 설명입니다." },
          // { property: "og:image", content: "// 이미지 주소" },
        ]}
      >
        <title>{edges[0].node.frontmatter.title}</title>
        <meta charSet="utf-8" />
      </Helmet>
      {edges && (
        <div
          css={[context_block_css, markdown_github_style_css]}
          dangerouslySetInnerHTML={{ __html: edges[0].node.html }}
        />
      )}
      <TableOfContents tocData={edges[0].node.tableOfContents} />
    </div>
  )
}

export const pageQuery = graphql`
  query ($title: String) {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___created] }
      limit: 1000
      filter: { frontmatter: { title: { eq: $title }, tags: { nin: "temp" } } }
    ) {
      edges {
        node {
          frontmatter {
            title
            tags
            created
            modified
          }
          html
          tableOfContents
        }
      }
    }
  }
`

